.explosion {
  width: 100vw;
  height: 100vh;
  min-width: 1023px;
  min-height: 600px;
  color: #000;
  z-index: 999;
  opacity: 0;
  background: #fff;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
}

.explosion_Opened, .explosion_Opening {
  display: block;
}

.explosion_Opening .explosionImage {
  transition: none;
}

.explosionControls {
  width: 48px;
  z-index: 7;
  height: 400px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 3000px;
  transition: margin .7s;
  display: flex;
  position: absolute;
  top: 0;
  right: 22%;
}

.explosionClose {
  width: 48px;
  height: 48px;
  cursor: pointer;
  background: url("close.a445a610.svg") center / 80% no-repeat;
  border: none;
  position: absolute;
  top: 0;
}

.explosionNavs {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.explosionNav {
  width: 48px;
  height: 48px;
  cursor: pointer;
  opacity: 1;
  background-color: rgba(0, 0, 0, 0);
  background-position: center;
  background-repeat: no-repeat;
  background-size: 80%;
  border: 1px solid #000;
  border-radius: 50%;
  transition: background-color .3s, opacity .3s;
}

.explosionNav:hover {
  background-color: #000;
}

.explosionNav:disabled {
  opacity: .3;
  pointer-events: none;
}

.explosionNavPrev {
  background-image: url("arrow-up.6985793d.svg");
}

.explosionNavPrev:hover {
  background-image: url("arrow-up-white.3bd44f23.svg");
}

.explosionNavNext {
  background-image: url("arrow-down.bb2d0f00.svg");
}

.explosionNavNext:hover {
  background-image: url("arrow-down-white.e9309a33.svg");
}

.explosionCounter {
  text-transform: lowercase;
  padding: 1rem 0;
  font-size: 1.2rem;
  font-weight: 700;
}

.explosionImage {
  object-fit: cover;
  width: 500px;
  z-index: 2;
  transform-origin: 50%;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  font-size: 0;
  transition: transform .7s, opacity .7s;
  display: block;
  position: absolute;
  transform: translate3d(0, 0, 0)scale(1);
}

.explosionSummary {
  z-index: 9;
  color: #fff;
  width: 0;
  background: #2d5051;
  transition: width 1s;
  position: absolute;
  bottom: 0;
  left: 0;
  overflow: hidden;
}

.explosionSummaryContent {
  opacity: 1;
  white-space: nowrap;
  padding: .5rem 5rem;
  transition: opacity .5s;
}

.explosionTitle {
  text-transform: uppercase;
  letter-spacing: .1em;
  margin: 0;
  font-size: 1rem;
}

.explosionDescription {
  color: #daa35b;
  margin: 0;
}

